.header {}

.header__wrapper {
  padding-top: 18px;

  @media screen and (min-width: @extra-large) {
    padding-top: 25px;
  }
}

.header__navigation-wrapper {
  display: flex;
  justify-content: space-between;
}

.header__wrapper-inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media screen and (min-width: @extra-large) {
    align-items: flex-end;
  }
}

.header__user-block {
  display: flex;
  justify-content: flex-end;
  align-items: center;

  @media screen and (min-width: @extra-large) {
    padding-bottom: 10px;
  }
}

.header__user-list {
  display: flex;
  align-items: center;
  margin-right: 15px;

  @media screen and (min-width: @extra-large) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.header__user-list-item {
  margin-right: 15px;
  width: 24px;
  height: 24px;
  position: relative;

  &:nth-child(3) {
    display: none;

    @media screen and (min-width: @extra-large) {
      display: block;
    }
  }
}

.header__user-cart--active {
  &::after {
    content: "";
    width: 10px;
    height: 10px;
    display: block;
    background-color: @red;
    position: absolute;
    top: -5px;
    right: -5px;
    border-radius: 50%;
  }
}

.header__navigation {
  width: 100%;
  max-width: 100%;
  display: none;
  margin-top: 40px;

  @media screen and (min-width: @extra-large) {
    display: block;
    margin-top: 0;
  }
}

.header__navigation-list {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-bottom: 40px;

  @media screen and (min-width: @medium) {
    padding-top: 20px;
  }

  @media screen and (min-width: @extra-large) {
    flex-direction: row;
    justify-content: space-between;
    max-width: 768px;
    margin-left: auto;
    padding-top: 0;
    padding-bottom: 0;
    border-top: 1px solid #445D77;
  }

  &::after {
    content: "";
    display: block;
    width: 80%;
    height: 2px;
    background-color: #E6E6EC;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);

    @media screen and (min-width: @medium) {
      width: calc(100% + 40px);
      margin-left: -20px;
      left: 0;
      transform: none;
    }

    @media screen and (min-width: @extra-large) {
      display: none;
    }
  }
}

.header__navigation-list-item {
  margin-top: 16px;
  margin-bottom: 16px;
  padding-left: 40px;

  @media screen and (min-width: @medium) {
    margin-top: 22px;
    margin-bottom: 22px;
  }

  @media screen and (min-width: @extra-large) {
    margin-top: 10px;
    margin-bottom: 0;
    padding-left: 0;

    &:hover,
    &:active {
      .header__navigation-link {
        color: @red;
      }
    }
  }
}

.header__navigation-link {
  font-weight: 500;
  color: @black;
  font-size: 28px;

  @media screen and (min-width: @extra-large) {
    font-size: 15px;
    line-height: 18px;
    color: @white;
  }
}

.header__contacts {
  display: none;

  @media screen and (min-width: @medium) {
    display: flex;
    flex-direction: row-reverse;
    margin-bottom: -10px;

    .header__mail-icon {
      width: 28px;
      height: 17px;
    }

    .header__mail-icon-black {
      display: none;
    }

    .header__mail-link {
      font-size: 0;
      margin-right: 15px;
    }

    .header__tel-link {
      margin-right: 20px;
      color: @white;
      font-size: 19px;
      line-height: 22px;
    }
  }

  @media screen and (min-width: @extra-large) {
    margin-bottom: 0;
    margin-right: 65px;

    .header__mail-icon {
      display: none;
    }

    .header__mail-link {
      font-size: 19px;
      line-height: 22px;
      margin-right: 15px;
      color: @white;
    }
  }
}

.header__contacts-mobile {
  display: flex;
  flex-direction: column;
  padding-left: 40px;
  margin-bottom: 80px;
  margin-top: 30px;

  .header__mail-link {
    font-size: 17px;
    line-height: 20px;
    color: @black;
    font-family: "Arial", sans-serif;
    margin-bottom: 20px;
  }

  .header__tel-link {
