  line-height: 25px;
  color: #8694A1;
  font-family: "Arial", sans-serif;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 25px;

  @media screen and (min-width: @medium) {
    padding-top: 25px;
  }

  @media screen and (min-width: @extra-large) {
    display: none;
  }
}

.header__logo {
  width: 140px;
  height: 32px;

  @media screen and (min-width: 374px) {
    width: 160px;
  }

  @media screen and (min-width: @medium) {
    height: 40px;
    width: 180px;
  }

  @media screen and (min-width: @extra-large) {
    height: 62px;
    width: 317px;
    margin-bottom: -27px;
  }
}

.header__logo-img {
  width: 160px;
  height: 32px;
  object-fit: cover;

  @media screen and (min-width: @medium) {
    height: 40px;
    width: 180px;
  }

  @media screen and (min-width: @extra-large) {
    height: 62px;
    width: 317px;
  }
}

.logo-path {
  fill: #fff;
}

.header__search-icon-black {
  display: none;
}

.header__cart-icon-black {
  display: none;
}

.header__user-icon-black {
  display: none;
}

.header__mobile-btn {
  height: 25px;
  width: 30px;
  position: relative;
  background-color: transparent;
  border: none;
  outline: none;
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
  }
}

.header__mobile-btn--gamburger {
  animation: scale 0.3s;

  height: 25px;
  width: 30px;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @white;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @white;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: block;
    width: 100%;
    height: 3px;
    background-color: @white;
    position: absolute;
    top: 11px;
    right: 0;
    border-radius: 1px;
  }
}

.header__mobile-btn--close {
  animation: scale2 0.4s;

  &::after {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @black;
    position: absolute;
    top: 11px;
    left: 0;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @black;
    position: absolute;
    bottom: 11px;
    left: 0;
    transform: rotate(135deg);
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: none;
  }
}

.show {
  animation: show 0.4s;
  margin-left: 0px;
}

.hide {
  @media screen and (max-width: @extra-large) {
    animation: hide 0.4s;
    margin-left: -300px;
  }
}

@keyframes scale {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale2 {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes show {
  0% {
    margin-left: -300px;
  }

  100% {
    margin-left: 0;
  }
}

@keyframes hide {
  0% {
    margin-left: 0;
  }

  100% {
    margin-left: -300px;
  }
}

