.slider {}

.swiper-button-next {
  display: none;

  @media screen and (min-width: @medium) {
    display: block;
    background-color: @red;
    width: 80px;
    height: 70px;
    top: 79%;
    right: auto;
    left: 80px;
    background-image: url("../img/slider-arrow-right.png");
    background-size: cover;
    background-position: 50%;

    &::after {
      opacity: 0;
    }
  }

  @media screen and (min-width: @extra-large) {
    z-index: 3;
    top: 50%;
  }
}

.swiper-button-prev {
  display: none;

  @media screen and (min-width: @medium) {
    display: block;
    background-color: @red;
    width: 80px;
    height: 70px;
    top: 79%;
    left: 0;
    background-image: url("../img/slider-arrow-left.png");
    background-size: cover;
    background-position: 50%;

    &::after {
      opacity: 0;
    }
  }

  @media screen and (min-width: @extra-large) {
    z-index: 3;
    top: 50%;
  }
}

.slider--about-us {
  padding-bottom: 40px;

  .swiper-button-prev {
    display: none;

    @media screen and (min-width: @medium) {
      display: block;
      top: 85%;
      left: 20px;
    }

    @media screen and (min-width: @extra-large) {
      top: 88.2%;
      left: 40px;
    }
  }

  .swiper-button-next {
    display: none;

    @media screen and (min-width: @medium) {
      top: 85%;
      display: block;
      left: 100px;
    }

    @media screen and (min-width: @extra-large) {
      top: 88.2%;
      left: 120px;
    }
  }

  .slider__item-wrapper {
    display: flex;
    flex-direction: column;

    @media screen and (min-width: @medium) {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }

  .slider__left-column {
    height: 332px;
    position: relative;
    margin-bottom: 20px;

    @media screen and (min-width: @medium) {
      width: 340px;
    }

    @media screen and (min-width: @extra-large) {
      width: 430px;
      height: 432px;
    }
  }

  .header__mail-icon {
    width: 100%;
    height: 100%;
  }

  .slider__int {
    display: block;
    position: absolute;
    font-size: 200px;
    line-height: 1;
    font-weight: 800;
    color: @red;
    top: 50%;
    left: 50%;
    transform: translate(-60%; -50%);
  }

  .slider__year {
    display: block;
    position: absolute;
    right: -20px;
    top: 50%;
    font-size: 52px;
    line-height: 1;
    color: @black;
    text-transform: uppercase;
    transform: rotate(-90deg);
  }

  .slider__right-column {
    @media screen and (min-width: @medium) {
      width: 48%;
    }
  }

  .slider__title {
    font-size: 25px;
    line-height: 32px;
    color: @black;
    margin-bottom: 20px;
  }

  .slider__txt {
    font-size: 19px;
    line-height: 32px;
  }
}

.swiper-pagination-bullet {
  border-radius: 0;
}

.swiper-pagination-bullet-active {
  background-color: @red;
}

.slider--long {
  padding-bottom: 40px;

  @media screen and (min-width: @medium) {
    padding-top: 40px;
    background: linear-gradient(180deg, rgba(244, 244, 246, 1) 62%, rgba(255, 255, 255, 1) 50%);
  }

  .slider__item {
    height: 150px;
    background-color: @white;
    position: relative;

    @media screen and (min-width: @medium) {
      //width: 180px!important;
    }

    @media screen and (min-width: @extra-large) {}
  }

  .slider__item-img--long {
    width: 85px;
    height: 92px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%; -50%);
  }

  .slider__wrapper.swiper-wrapper {
    @media screen and (min-width: @medium) {
      height: 300px;
      padding-left: 40px;
    }
  }
}

.swiper-container-horizontal>.swiper-pagination-progressbar,
.swiper-container-vertical>.swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  @media screen and (min-width: @medium) {
    top: 89.5%;
    left: 30%;
    width: 70%;
  }

  @media screen and (min-width: @extra-large) {
    top: 62%;
    left: 300px;
    width: 80%;
  }
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background-color: @red;
}

.swiper-container-horizontal>.swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  @media screen and (min-width: @medium) {
    width: calc(100% - 200px);
    left: 180px;
    background-color: @white;
    padding-top: 24px;
    padding-bottom: 24px;
  }

  @media screen and (min-width: @extra-large) {
    left: 200px;
  }
}

// слайдер на странице about-us
.slider--direction-about-us {
  padding-bottom: 50px;

  .slider__item-wrapper {
    display: flex;
    flex-direction: column;

    @media screen and (min-width: @medium) {
      display: flex;
      flex-direction: row;
      align-items: center;
    }
  }

  .slider__left-column {
    margin-bottom: 20px;

    @media screen and (min-width: @medium) {
      width: 48%;
    }

    @media screen and (min-width: @extra-large) {
      width: 382px;
    }
  }

  .slider__right-column {
    @media screen and (min-width: @medium) {
      width: 48%;
      padding-left: 40px;
    }

    @media screen and (min-width: @extra-large) {
      margin-top: -60px;
    }
  }

  .slider__img {
    @media screen and (min-width: @medium) {
      margin-bottom: 34px;
      object-fit: cover;
      max-height: 364px;
    }
  }

  .slider__title {
    font-size: 25px;
    line-height: 1.3;
    margin-bottom: 20px;
    color: @black;
    font-weight: 600;
  }

  .slider__txt {
    color: @black;
  }

  .swiper-container-horizontal>.swiper-pagination-bullets,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    @media screen and (min-width: @medium) {
      width: 52%;
      left: auto;
      right: 0;
      bottom: 0;
      top: 62%;
      height: 70px;
    }
  }

  .swiper-button-next {
    top: 66.5%;
    right: 52%;
    left: auto;
  }

  .swiper-button-prev {
    top: 66.5%;
    right: calc(52% + 80px);
    left: auto;
  }
}

.slider--advantages-long {
  background: none;

  .slider__wrapper.swiper-wrapper {
    @media screen and (min-width: @extra-large) {
      padding-left: 0;
    }
  }

  .slider__item {
    background-color: transparent;
    height: 290px;

    @media screen and (min-width: @medium) {
      height: 150px;
    }
  }

  .slider__item-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;

    @media screen and (min-width: @medium) {
      flex-direction: row;
    }
  }

  .slider__img-wrapper {
    background-color: @white;
    height: 200px;
    width: 245px;

    @media screen and (min-width: @medium) {
      width: 180px;
      height: 100%;
    }
  }

  .slider__item-img--long {
    position: static;
    top: 0;
    left: 0;
    transform: translate(-50%, -70%);
    margin-left: 50%;
    margin-top: 50%;
  }

  .slider__bold {
    color: @black;
    width: 70%;
    font-size: 19px;
    line-height: 1.3;
    margin-top: 20px;
    margin-left: 30px;
  }

  .swiper-pagination.swiper-pagination-progressbar {
    @media screen and (min-width: @extra-large) {
      top: 78%;
    }
  }

  .swiper-button-next {
    @media screen and (min-width: @extra-large) {
      top: 67%;
    }
  }

  .swiper-button-prev {
    @media screen and (min-width: @extra-large) {
      top: 67%;
    }
  }
}

.slider__red-txt {
  color: @red;
}

.slider--history {
  @media screen and (min-width: @medium) {
    padding-bottom: 40px;

    .slider__item-wrapper {
      align-items: center;
    }

    .slider__left-column {
      margin-bottom: 0;
      min-height: 382px;
    }

    .slider__img {
      margin-bottom: 0;
      max-height: 374px;
    }

    .slider__right-column {
      margin-top: -140px;
    }
  }
}
