.index-info {
  padding-top: 0px;
  padding-bottom: 40px;

  @media screen and (min-width: @medium) {
    background-color: @white;
    padding-top: 100px;
  }

  @media screen and (min-width: @extra-large) {
    padding-bottom: 0;
  }
}

.index-info__title {
  font-size: 30px;
  line-height: 42px;
  color: @black;
  margin-bottom: 30px;
  margin-top: 0;

  @media screen and (min-width: @extra-large) {
    margin-bottom: -40px;
    position: relative;
    z-index: 3;
  }
}

.index-info__warpper {
  padding-left: 15px;

  @media screen and (min-width: @extra-large) {
    margin-left: 2vw;
    // display: flex;
  }

  @media screen and (min-width: @laptop) {
    margin-left: 6vw;
    // display: flex;
  }

  @media screen and (min-width: 1680px) {
    margin-left: 14.5vw;
    // display: flex;
  }

  @media screen and (min-width: @desktop) {
    margin-left: 18.5vw;
    // display: flex;
  }
}

.index-info__slider-long {
  @media screen and (min-width: @extra-large) {
    padding-left: 300px;
    position: relative;
    z-index: 1;

    &::after {
      content: "";
      display: block;
      width: 300px;
      height: 340px;
      background-color: @white;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
    }
  }
}
