.table__title-row {
  display: flex;
  justify-content: space-between;
  color: #9AA0A7;
  font-size: 13px;
  line-height: 22px;
  border-bottom: 1px solid #E5E5E5;

  @media screen and (min-width: @medium) {}
}

.table__title-row-item--name {
  @media screen and (min-width: @medium) {
    font: inherit;
    width: 48%;
  }
}

.table__title-row-item--type {
  font-size: 0;

  @media screen and (min-width: @medium) {
    font: inherit;
  }
}

.table__title-row-item--date {
  display: none;

  @media screen and (min-width: @medium) {
    display: block;
    font: inherit;
    margin-right: 14%;
  }
}

.table__hidden-txt {
  font-size: 15px;

  @media screen and (min-width: @medium) {
    font-size: 0;
  }
}

.table__row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  border-bottom: 1px solid #E5E5E5;
  padding-top: 13px;
  padding-bottom: 17px;
  font-size: 15px;
  line-height: 25px;
  font-family: "Arial", sans-serif;
  background-color: #FBFBFB;

  &:last-child {
    border: none;
  }

  @media screen and (min-width: @medium) {
    flex-wrap: nowrap;
    align-items: flex-start;
  }
}

.table__manufacturer {
  color: #596673;
}

.table__row-item--title {
  width: 48%;
  color: @black;

  @media screen and (min-width: 513px) {
    width: 78%;
  }

  @media screen and (min-width: @medium) {
    width: 48%;
    padding-left: 40px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 30px;
      height: 36px;
      background-image: url("../img/svg/documents-icon.svg");
      background-repeat: no-repeat;
      background-size: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.table__row-item--type {
  width: 50%;
  margin-top: 40px;
  text-align: right;
  color: #596673;

  @media screen and (min-width: 513px) {
    width: 100%;
    text-align: right;
  }

  @media screen and (min-width: @medium) {
    width: auto;
    margin-top: 0;
    padding-top: 22px;
  }
}

.table__row-item--date {
  width: 48%;
  margin-left: auto;
  margin-top: -50px;
  text-align: right;
  color: #596673;

  @media screen and (min-width: @medium) {
    width: auto;
    margin-top: 0;
    margin-left: 0;
    padding-top: 22px;
  }
}

.table__row-item--download {
  text-align: center;
  width: 100%;

  @media screen and (min-width: 513px) {
    width: 100%;
    text-align: right;
  }

  @media screen and (min-width: @medium) {
    width: auto;
    padding-top: 22px;
  }

  .table__download {
    font-family: "Arial", sans-serif;
    color: @red;

    @media screen and (min-width: @medium) {
      color: #596673;
      font-size: 17px;
      line-height: 1.3;
    }
  }
}
