.advantages {
  padding-top: 80px;
  padding-bottom: 90px;
}

.advantages__wrapper {
  @media screen and (min-width: @extra-large) {
    padding-right: 404px;
  }
}

.advantages__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 40px;
}

.advantages__list-item {
  width: 48%;
  margin-bottom: 20px;
}

.advantages__slogan {
  font-size: 30px;
  font-weight: 700;
  line-height: 42px;
  text-transform: uppercase;
  color: @black;
  margin-top: 0;
}

.advantages__icon-wrapper {
  width: 60px;
  height: 70px;
  position: relative;
  margin-bottom: 30px;

  &::after {
    content: "";
    display: block;
    width: 50px;
    height: 50px;
    background-color: @white;
    border-radius: 50%;
    position: absolute;
    bottom: 0;
    right: 0;
  }
}

.advantages__icon {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.advantages__bold {
  font-size: 19px;
  line-height: 1.3;
  font-weight: 700;
  color: @black;
  margin-bottom: 20px;
  display: block;
}

.advantages__present {
  background-color: @white;
  padding: 20px;
  position: relative;
  width: calc(100% + 30px);
  margin-left: -15px;
  margin-bottom: 90px;

  @media screen and (min-width: @medium) {
    padding: 35px;
    width: calc(100% + 40px);
    margin-left: -20px;
  }

  &::after {
    content: "";
    width: 60%;
    height: 90%;
    background-image: url("../img/svg/present-img-bottom.svg");
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-47%);

    @media screen and (min-width: @medium) {
      width: 200px;
      height: 200px;
      right: 10%;
    }
  }

  &::before {
    content: "";
    width: 60%;
    height: 90%;
    background-image: url("../img/svg/present-img-top.svg");
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
    position: absolute;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);
    z-index: 2;

    @media screen and (min-width: @medium) {
      width: 200px;
      height: 200px;
      right: calc(10% + 5px);
    }
  }
}

.advantages__present-dscr {
  width: 38%;

  @media screen and (min-width: @medium) {
    width: 60%;
  }
}

.advantages__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 42px;
  color: @black;
  text-transform: uppercase;
}

.advantages--history {
  padding-top: 0;
}
