.equipment {
  padding-top: 80px;
}

.equipment__title-wrapper {
  @media screen and (min-width: @extra-large) {
    display: flex;
    justify-content: space-between;
  }
}

.equipment__title {
  font-size: 30px;
  line-height: 42px;
  font-weight: 800;
  text-transform: uppercase;
  margin-bottom: 20px;
  color: @black;

  @media screen and (min-width: @extra-large) {
    margin-top: 0;
  }
}

.equipment__title-dscr {
  font-size: 19px;
  line-height: 32px;
  margin-bottom: 20px;

  @media screen and (min-width: @extra-large) {
    width: 100%;
    max-width: 820px;
  }
}

.equipment__list {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: @medium) {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
