.product-card {
  padding-top: 35px;
  padding-left: 35px;
  padding-right: 48px;
  padding-bottom: 40px;
  background-color: @white;
  margin-bottom: 20px;
  transition: all ease-in-out 0.3s;

  @media screen and (min-width: @medium) {
    max-width: 344px;
    width: 100%;
    margin-bottom: 40px;
  }

  @media screen and (min-width: @extra-large) {
    max-width: 560px;
    padding-top: 42px;
    padding-left: 80px;
    padding-right: 74px;
    padding-bottom: 72px;

    &:hover {
      .product-card__image-wrapper {
        margin-top: -70px;
      }

      .product-card__title {
        margin-bottom: 40px;
      }
    }
  }
}

.product-card__image-wrapper {
  width: 60%;
  margin-left: auto;
  margin-bottom: 32px;
  transition: all ease-in-out 0.3s;

  @media screen and (min-width: @medium) {
    height: 294px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  @media screen and (min-width: @extra-large) {
    margin-bottom: 0;
    height: 254px;
  }
}

.product-card__img {
  @media screen and (min-width: @medium) {
    width: 80%;
  }

  @media screen and (min-width: @extra-large) {
    width: 50%;
  }
}

.product-card__count {
  font-size: 15px;
  line-height: 1.7;
  color:  #A8ACAF;
  font-family: "Arial", sans-serif;
}

.product-card__title {
  margin-top: 0;
  margin-bottom: 30px;
  color: @black;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.3;
  transition: all ease-in-out 0.3s;

  @media screen and (min-width: @extra-large) {
    margin-bottom: -30px;
    position: relative;
    z-index: 2;
    background-color: @white;
  }
}

.product-card__link {
  color: @red;
  position: relative;
  padding-left: 30px;
  font-size: 0;
  height: 20px;

  @media screen and (min-width: @extra-large) {
    font-size: 15px;
    line-height: 18px;
    position: relative;
    z-index: 1;
  }

  &::before {
    content: "";
    display: block;
    width: 20px;
    height: 2px;
    background-color: red;
    position: absolute;
    top: 9px;
    left: 0;
  }

  &::after {
    content: "";
    display: block;
    width: 2px;
    height: 20px;
    background-color: red;
    position: absolute;
    top: 0px;
    left: 9px;
  }
}
