.video {
  background-color: #FBFBFB;
  padding-top: 30px;
  padding-bottom: 40px;

  @media screen and (min-width: @medium) {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media screen and (min-width: @extra-large) {
    padding-top: 120px;
    padding-bottom: 130px;
  }
}

.video__list {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: @medium) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.video__filter-wrapper {
  margin-bottom: 40px;

  .select2-container {
    width: 100%!important;
    max-width: 560px!important;
    border: 1px solid #F4F4F6;
    height: 40px;

    @media screen and (min-width: @medium) {
      width: 48%!important;
    }
  }
}

.video__list-item {
  position: relative;
  margin-bottom: 20px;
  width: 100%;

  @media screen and (min-width: @medium) {
    width: 48%;
  }

  @media screen and (min-width: @extra-large) {
    max-width: 560px;
  }

  &:hover {
    .video__list-link {
      color: @red;
    }
  }
}

.video__btn {
  background-color: rgba(218,54,50,0.7);
  position: absolute;
  top: 0;
  left: 0;
  height: 315px;
  width: 100%;
  font-size: 15px;
  font-weight: 500;
  color: @white;
  z-index: 2;
  border: none;
  cursor: pointer;
  display: none;
}

.video__btn-decor {
  display: inline-block;
  padding-left: 30px;
  background-image: url("../img/svg/play-icon.svg");
  background-repeat: no-repeat;
  background-size: 20px 20px;
  padding-bottom: 5px;
}

.video__list-date {
  color: #596673;
  font-size: 13px;
  line-height: 23px;
}

.video__list-link {
  font-size: 19px;
  line-height: 1.3;
  font-weight: 700;
  color: @black;
  width: 80%;
  display: block;
  transition: all ease-in 0.3s;
}

.video__list-item-video {
  width: 100%;
  object-fit: cover;
}
