.direction {
  padding-top: 70px;
}

.direction__wrapper {
  @media screen and (min-width: @extra-large) {
    padding-right: 404px;
  }
}

.direction__title-wrapper {
  margin-bottom: 42px;

  @media screen and (min-width: @medium) {
    display: flex;
    justify-content: space-between;
  }
}

.direction__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 42px;
  color: @black;
  text-transform: uppercase;
  margin-top: 0;
}

.direction__dscr-title {
  font-weight: 400;
  font-size: 19px;
  line-height: 1.7;
  color: @black;

  @media screen and (min-width: @medium) {
    width: 48%;
  }
}

.direction__slider {
  margin-bottom: 60px;

  @media screen and (min-width: @medium) {
    margin-bottom: 0;
  }
}

.direction__info {
  @media screen and (min-width: @medium) {
    display: flex;
    justify-content: space-between;
  }
}

.direction__info-dscr {
  padding-bottom: 20px;

  @media screen and (min-width: @medium) {
    width: 48%;
  }
}
