.news-index {
  background-color: white;
  padding-top: 70px;
  padding-bottom: 80px;

  @media screen and (min-width: @extra-large) {
    padding-top: 0;
  }
}

.news-index__title-wrapper {
  margin-bottom: 30px;

  @media screen and (min-width: @medium) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  @media screen and (min-width: @extra-large) {
    justify-content: flex-start;
  }
}

.news-index__all-news-link {
  display: none;

  @media screen and (min-width: @medium) {
    display: block;
    font-size: 17px;
    line-height: 1.3;
    color: @red;
    font-weight: 500;
    position: relative;
    padding-right: 30px;
    width: 138px;
    margin-left: 40px;

    &::after {
      content: "";
      width: 15px;
      height: 10px;
      position: absolute;
      background-image: url("../img/red-arrow.png");
      background-size: contain;
      background-position: 50%;
      background-repeat: no-repeat;
      top: 6px;
      right: 0;
    }
  }
}

.news-index__all-news-link-mobile {
  display: block;
  font-size: 17px;
  line-height: 1.3;
  color: @red;
  font-weight: 500;
  position: relative;
  padding-right: 30px;
  width: 138px;
  margin: 0 auto;

  &::after {
    content: "";
    width: 15px;
    height: 10px;
    position: absolute;
    background-image: url("../img/red-arrow.png");
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
    top: 6px;
    right: 0;
  }

  @media screen and (min-width: @medium) {
    display: none;
  }
}

.news-index__title {
  font-size: 30px;
  line-height: 42px;
  color: @black;
  font-weight: 700;
  text-transform: uppercase;

  @media screen and (min-width: @extra-large) {
    width: 100%;
    max-width: 560px;
  }
}
