.documents {
  padding-top: 30px;
  background-color: #FBFBFB;
  padding-bottom: 30px;

  @media screen and (min-width: @medium) {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media screen and (min-width: @extra-large) {
    padding-top: 120px;
    padding-bottom: 140px;
  }
}

.documents__filter-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;

  @media screen and (min-width: @medium) {
    margin-bottom: 40px;
  }

  @media screen and (min-width: @extra-large) {
    justify-content: flex-start;
    margin-bottom: 100px;
  }
}

.documents__filter-select-wrapper {
  width: 48%;
  max-width: 220px;
  position: relative;

  @media screen and (min-width: @medium) {
    max-width: 343px;
  }

  @media screen and (min-width: @extra-large) {
    max-width: 220px;
    margin-right: 20px;
  }
}

.documents__filter-btn {
  width: 100%;
  position: relative;
  font-size: 15px;
  line-height: 25px;
  font-family: "Arial", sans-serif;
  padding: 7px;
  padding-left: 10px;
  text-align: left;
  border: 1px solid #F4F4F6;
  border-radius: 0;
  background-color: @white;

  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 15px;
    background-image: url("../img/svg/link-arrow.svg");
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 12px;
    transform: rotate(270deg);
    right: 12px;
    transition: all ease-in 0.3s;
  }
}

.documents__filter-btn--active {
  &::after {
    transform: rotate(0deg);
    transition: all ease-in 0.3s;
  }
}

.documents__filter-list {
  display: none;
  background-color: @white;
  padding-left: 20px;
  padding-right: 20px;
  border: 1px solid #F4F4F6;
  border-top: none;
  padding-bottom: 20px;
  position: absolute;
  width: 100%;
  box-shadow: 0px 4px 4px 0px #DBDBDF;
}

.documents__filter-list-item {
  padding-top: 7px;
  padding-bottom: 7px;
  border-bottom: 1px solid #F4F4F6;
}

.documents__checkbox {
  display: none;
}

.documents__label {
  position: relative;
  width: 100%;
  display: block;

  &::after {
    content: "";
    display: block;
    width: 11px;
    height: 11px;
    background-color: #E7E7EA;
    position: absolute;
    top: 6px;
    right: 0;
  }
}

.documents__checkbox:checked {
  + .documents__label {
    &::after {
      content: "";
      display: block;
      width: 11px;
      height: 11px;
      background-color: @red;
      position: absolute;
      top: 6px;
      right: 0;
    }
  }
}
