.about-us-start-info {
  padding-top: 40px;
}

.about-us-start-info__wrapper {
  display: flex;
  flex-direction: column-reverse;
}

.about-us-start-info__history {
  @media screen and (min-width: @medium) {
    display: flex;
    justify-content: space-between;
    width: 256px;
    margin-left: auto;
    margin-bottom: -80px;
  }

  @media screen and (min-width: @extra-large) {
    flex-direction: column;
    width: 156px;
  }
}

.about-us-start-info__history-link {
  display: block;
  font-weight: 600;
  color: @black;
  margin-bottom: 30px;
  width: 110px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 8px;
    height: 10px;
    background-image: url("../img/svg/link-arrow.svg");
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
  }
}

.about-us-start-info__history-dscr {
  opacity: 0.5;
}

.about-us-start-info__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 42px;
  color: @black;
  text-transform: uppercase;
}

.about-us-start-info__dscr {
  @media screen and (min-width: @extra-large) {
    width: 100%;
    max-width: 876px;
  }
}
