.present {
  padding-top: 40px;
  padding-bottom: 40px;

  @media screen and (min-width: @medium) {
    padding-bottom: 100px;
  }
}

.present__wrapper {
  overflow-x: hidden;
}

.present__slogan {
  width: 700px;
  margin-left: -120px;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;

  @media screen and (min-width: @medium) {
    width: 1100px;
  }

  @media screen and (min-width: @extra-large) {
    width: 1900px;
    margin-left: 0;
  }
}

.present__sogan-txt {
  color: @white;
  font-size: 45px;
  line-height: 1.4;
  text-transform: uppercase;

  &:nth-child(2) {
    text-align: right;
  }

  @media screen and (min-width: @medium) {
    font-size: 90px;

    &:nth-child(2) {
      margin-right: -146px;
    }
  }

  @media screen and (min-width: @extra-large) {
    font-size: 130px;
    line-height: 1;

    &:nth-child(1) {
      text-align: left;
    }

    &:nth-child(2) {
      margin-right: 0;
      text-align: right;
    }
  }
}
