.news-list {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @media screen and (min-width: @extra-large) {
    // flex-direction: row;
    // justify-content: space-between;
    // flex-wrap: wrap;
    // align-content: stretch;
    display: grid;
    gap: 40px;
    grid-template-columns: 560px 1fr;
    grid-template-rows: 280px;
  }

  .news-list__item {
    position: relative;
    min-height: 365px;
    margin-bottom: 40px;

    @media screen and (min-width: @medium) {
      min-height: 280px;
    }

    @media screen and (min-width: @extra-large) {
      &:nth-child(1) {
        width: 560px;
        height: 600px;
      }

      &:nth-child(2) {
        max-width: 560px;
        height: 280px;
        margin-bottom: 0;
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 3;
      }

      &:nth-child(3) {
        max-width: 560px;
        height: 280px;
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
      }
    }
  }

  .news-list__item-img {
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 0;
    height: calc(100% - 1px);
    object-fit: cover;
  }

  .news-list__item-wrapper {
    background-image: url("../img/news-item-shadow-small.png");
    background-size: cover;
    min-height: 365px;
    position: relative;
    z-index: 2;
    display: flex;

    @media screen and (min-width: @extra-large) {
      min-height: 100%;
    }
  }

  .news-list__txt-wrapper {
    margin-top: auto;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 38px;

    @media screen and (min-width: @medium) {
      max-width: 60%;
    }
  }

  .news-list__date {
    font-size: 15px;
    line-height: 1.7;
    color: @white;
    font-family: "Arial", sans-serif;
    margin-bottom: 10px;
  }

  .news-list__link {
    font-size: 22px;
    line-height: 1.3;
    color: @white;

    &:hover,
    &:active {
      background-color: @red;
    }
  }
}

